import React,{Component} from 'react';
import axios from 'axios';
import ListingDisplay from './ListingDisplay';
import CostFilter from '../filters/costfilter';
import CuisineFilter from '../filters/cuisinefilter';
const url="https://resterapp.herokuapp.com/restaurants?mealtype=";
const murl="https://resterapp.herokuapp.com/mealtype";
class ListingApi extends Component{
    constructor(props){
        super()
        this.state ={
            hotelist:'',
            mealname:''
        }
    }
    setDataAsPerFilter(sorteddata){
        this.setState({hotelist:sorteddata})
    }
    render(){
        return(
            <div className="container">
                <div class="progress">
                <div class="progress-bar progress-bar-success progress-bar-striped active" role="progressbar"
                style={{width:"30%"}}>
                    30% Complete
                </div>
                </div>
                <div className="row">
                <div className="col-md-2">
                    <CostFilter costdata = {(data) =>{this.setDataAsPerFilter(data)}}/>
                    <CuisineFilter cuisinedata={(data) => {this.setDataAsPerFilter(data)} } />
                </div>
                <div className="col-md-10">
                    <center>
                        <h3>Your's {this.state.mealname} are:</h3>
                    </center>
                    <ListingDisplay listdata={this.state.hotelist}/>
                </div>
            </div>
            </div>
            
        )
    }
    componentDidMount(){
        var mealtype = this.props.match.params.id
        sessionStorage.setItem('mealtype',mealtype)
        axios.get(`${murl}`)
        .then((response)=>{
            for(var i=0;i<response.data.length;i++){
                if(response.data[i]._id === Number(mealtype)){
                    this.setState({mealname:response.data[i].name.toUpperCase()})
                    i=response.data.length
                }else{
                    this.setState({mealname:'Wrong Input'})
                }
            }
            
        })
        axios.get(`${url}${mealtype}`)
        .then((response)=>{this.setState({hotelist:response.data})})
    }
}

export default ListingApi;